.contentWrapper {
    @apply px-52 py-20;
}

.orgTitle {
    @apply text-5xl text-eco-white font-semibold font-acumin-pro;
}

.orgStatsWrapper {
    @apply grid grid-cols-2 gap-7 mt-10 text-eco-white;
    @apply md:grid-cols-3;
    @apply lg:grid-cols-4;
}

.orgStatsTitle {
    @apply text-xs tracking-wide uppercase font-acumin-pro;
}

.orgStatsValue {
    @apply text-3xl font-acumin-pro mt-2;
}

.orgBadge {
    @apply bg-eco-purple p-2 w-28 h-28 flex flex-col justify-center items-center;
}

.orgBadgeIcon {
    @apply text-7xl text-eco-white;
}

.orgBadgeText {
    @apply text-xs text-eco-white text-center;
}

.orgJoinWrapper {
    @apply mt-10 border-t border-t-eco-white pt-10 grid gap-7;
    @apply md:grid-cols-2;
}

.orgJoinReason {
    @apply text-eco-green font-acumin-pro uppercase font-bold text-xs mb-2;
}

.orgJoinWait {
    @apply font-acumin-pro text-eco-white text-xl font-medium mb-5;
}