/* Top bar */

.topWrapper {
    @apply flex flex-row justify-between items-end px-10 py-8;
}

.logoWrapper {
    @apply flex flex-row items-end;
}

.logoIcon {
    @apply h-8 w-auto;
}

.logoText {
    @apply font-acumin-pro -mb-1.5 text-sm ml-2;
}

.topNavWrapper {
    @apply flex flex-col items-end;
}

.topNavUser {
    /* @apply; */
    @apply font-acumin-pro uppercase text-xs font-medium border-b border-b-transparent duration-300 ease-in-out;
}

.topNavUserLink {
    @apply cursor-pointer hover:border-b-eco-white ;
}

.topNavLinks {
    @apply flex flex-row my-1;
}

.topNavLink {
    @apply ml-4 hover:underline duration-300 ease-in-out;
}

.topNavLinkText {
    @apply font-acumin-pro font-medium text-sm tracking-wider;
}