.actionButton {
    @apply font-acumin-pro rounded-3xl px-8 py-2 text-sm text-eco-dark w-fit my-2 hover:animate-pulse duration-300 ease-in-out;
}

.submitButton {
    @apply font-acumin-pro bg-eco-dark px-10 py-1.5 rounded-3xl border-2 border-eco-white text-eco-white w-fit my-2 hover:animate-pulse duration-300 ease-in-out;
}