.footerWrapper {
    @apply mx-28 px-0 pt-16 pb-28 border-t mt-5 flex flex-row justify-between items-start border-eco-white text-eco-white;
}

.sectionOne {
    @apply w-1/3;
}

.sectionOneBadgeWrapper {
    @apply  py-0.5 w-36 text-center mb-5 rounded bg-white;
}

.sectionOneBadge {
    @apply flex flex-row items-center p-2 mx-0.5 rounded-b-xl bg-eco-dark;
}

.sectionOneBadgeIcon {
    @apply h-7 w-auto mr-1;
}

.sectionOneBadgeText {
    @apply font-acumin-pro text-xs text-left tracking-wider;
}

.sectionOneButton {
    @apply text-center mx-auto;
}

.sectionOneButtonText {
    @apply font-acumin-pro text-xs text-center text-eco-dark;
}

.sectionOneTitle {
    @apply font-acumin-pro font-medium w-2/3 my-2 text-xl;
}

.sectionOneCopyWrapper {
    @apply flex flex-row justify-start items-center;
}

.sectionOneCopy {
    @apply text-xs font-acumin-pro;
}

.sectionOneIcon {
    @apply ml-1 h-4;
}

.section {
    @apply ml-16;
}


.sectionLink {
    @apply text-opacity-60 text-sm font-acumin-pro text-eco-white;
}

.sectionTitle {
    @apply font-bold text-sm font-acumin-pro;
}

/* Community footer */

.sectionTwoTitle {
    @apply text-sm font-bold text-eco-white font-acumin-pro;
}

.sectionTwoInfo {
    @apply text-xs text-eco-white font-acumin-pro font-medium mt-6 w-52;
}