.sectionWrapper {
    @apply px-52 py-10 flex flex-row items-center justify-between;
}
.sectionTitle {
    @apply text-eco-white font-acumin-pro text-4xl font-bold mb-5;
}

.sectionSubTitle {
    @apply text-eco-white font-acumin-pro text-lg font-bold mb-2;
}

.sectionInfo {
    @apply text-eco-white font-acumin-pro text-sm mb-5;
}

.sectionLink {
    @apply text-sm my-5 font-acumin-pro w-fit text-eco-white border-b border-b-transparent hover:border-b-eco-white duration-300 ease-in-out;
}