.dropWrapper {
    @apply relative flex flex-col;
}

.dropLabel {
    @apply text-sm font-bold text-eco-white font-acumin-pro;
}

.dropButton {
    @apply font-acumin-pro flex w-52 flex-row justify-between items-center bg-eco-white bg-opacity-40 px-2 py-1.5 rounded text-eco-white;
}

.dropButtonIcon {

}

.dropList {
    @apply absolute top-16 -left-0 bg-eco-purple bg-opacity-90 rounded h-72 w-52 shadow-md drop-shadow-lg overflow-y-auto opacity-100 visible duration-300 ease-in-out;
}

.dropListHidden {
    @apply absolute top-16 -left-0 bg-eco-purple text-eco-white bg-opacity-90 rounded h-0 w-52 overflow-y-hidden opacity-0 invisible duration-300 ease-in-out;
}

.dropListItem {
    @apply px-2 mx-1 font-acumin-pro font-bold text-eco-dark py-1 bg-eco-purple text-eco-white bg-opacity-0 cursor-pointer text-left my-1 duration-500 ease-in-out hover:shadow-md;
}

.searchInput {
    @apply font-acumin-pro w-48 outline-none my-1 mx-1 bg-eco-purple text-white text-sm font-bold p-2 border-b;
}