.contentWrapper {
    @apply px-52 py-10 w-full;
}

.versionText {
    @apply text-eco-green text-sm text-center font-bold font-acumin-pro tracking-wide;
}

.contentTitle {
    @apply text-eco-white font-acumin-pro font-bold text-center text-6xl my-3;
}

.contentAbout {
    @apply text-center font-acumin-pro px-52 text-eco-white text-opacity-70 text-xl;
}

.contentReasonWrapper {
    @apply grid grid-cols-2 gap-2 mt-16 mb-10;
}

.contentReasonTitle {
    @apply  text-eco-white text-base font-bold font-acumin-pro;
}

.contentReasonText {
    @apply text-eco-white font-acumin-pro text-opacity-70 text-left px-5 text-base;
}

/* Yolo calculator */

.calculatorWrapper {
    @apply bg-eco-purple px-16 my-5 py-10;
}

.sectionWrapper {
    @apply flex flex-row justify-between items-center my-2 p-5 border-b border-b-eco-white;
}

.sectionSubWrapper {
    @apply w-full grid grid-cols-4 gap-5;
}

.sectionTitle {
    @apply mr-28 w-48 font-bold text-2xl text-eco-white font-acumin-pro;
}

.sectionLabel {
    @apply text-xs text-eco-white text-opacity-90 font-bold font-acumin-pro;
}

.sectionField {
    @apply bg-eco-white bg-opacity-40 outline-none rounded-md px-2 py-2 text-sm text-eco-white w-40 my-1;
}

.sectionSubTitle {
    @apply text-sm text-eco-white font-bold font-acumin-pro;
}

.sectionValue {
    @apply text-lg text-eco-white font-bold font-acumin-pro;
}

.sectionButton {
    @apply bg-eco-white my-2 px-16 py-1.5 rounded-3xl shadow-md text-eco-dark text-lg;
}

.aboutHelp {
    @apply font-acumin-pro text-eco-white text-xl pr-52 font-bold my-6;
}

